.menu-tool {
  background-color: #3da9fc;
  padding: 10px 10px 5px 10px;
  text-align: center;
  color: #fff;
  transition: 0.3s;
  cursor: pointer;
  margin-bottom: 10px;
  border-bottom: 2px solid #000;
  border-right: 2px solid #000;
}

.menu-tool:hover {
  filter: brightness(0.8);
}
