.navbar {
  position: sticky;
  top: 0;
  right: 0;
  background-color: #fff;
  z-index: 900;
}

.home {
  margin-left: 500px;
  margin-right: 20px;
  font-size: 18px;
  color: #094067;
  font-weight: 500;
}

.about {
  margin-right: 20px;
  font-size: 18px;
  color: #094067;
  font-weight: 500;
}
.admin {
  margin-right: 50px;
  font-size: 19px;
  color: #094067;
  font-weight: 500;
}

.logout {
  margin-right: 20px;
  font-size: 19px;
  color: #094067 !important;
  font-weight: 500;
}

.brandname {
  margin-left: 15px;
  font-size: 22px;
  font-weight: bold;
  color: #094067;
}

/* responsif */
@media (max-width: 477px) {
  .home {
    margin-left: 0;
  }
  .logout {
    font-size: 16px;
  }
  .home,
  .about,
  .admin {
    font-size: 16px;
  }
  .logo {
    width: 48px;
  }

  .brandname {
    font-size: 21px;
  }
}

@media (max-width: 991px) {
  .home,
  .about,
  .admin,
  .logout {
    margin-bottom: 10px;
  }
  .home {
    margin-left: 0;
  }
}
