.title {
  border: none;
  font-size: 22px !important;
  font-weight: bold;
  width: 100%;
  margin-top: 10px;
}

.title:focus,
.url:focus,
.category:focus {
  outline: none;
  border-bottom: 2px solid #272626;
}

.url,
.category {
  border: none;
  width: 100%;
  margin-top: 10px;
}

.title::placeholder {
  font-weight: normal;
  font-size: 20px;
}

.fa-plus {
  font-size: 30px;
  border: 2px solid #272626;
  padding: 5px 8px 5px 8px;
  border-radius: 50%;
  background-color: #edeaea;
  margin-right: 20px;
  margin-bottom: 20px;
}

.file-image {
  height: 200px;
  width: 100%;
  object-fit: cover;
  /* padding-right: 30px; */
}
