.writephoto {
  width: 100%;
  height: 50%;
  object-fit: cover;
  border-radius: 10px;
  margin-bottom: 20px;
}

.writeicon {
  width: 30px;
  height: 30px;
  font-size: 20px;
  border-radius: 50%;
  border: 1px solid;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  margin-right: 7px;
  margin-bottom: 10px;
}
