.write {
  padding-top: 50px;
}

.writeicon {
  width: 30px;
  height: 30px;
  font-size: 20px;
  border-radius: 50%;
  border: 1px solid;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  margin-right: 7px;
  margin-bottom: 10px;
}

.writeinput {
  font-size: 32px;
  font-weight: bold;
  border: none;
  padding: 15px;
  width: 96%;
  font-family: "Josefin Sans", sans-serif;
  border-bottom: 2px solid lightgray;
}
.writeinput:focus {
  outline: none;
}

.writeformgroup {
  padding: 20px;
  /* font-size: 30px; */
  font-family: "Varela Round", sans-serif;
}
.writetext {
  margin-top: 10px;
  width: 100%;
  border: none;
  font-size: 17px;
  /* padding: 20px; */
  /* max-height: 500px; */
  font-family: "Varela Round", sans-serif;
}

.writephoto {
  width: 100%;
  height: 40%;
  object-fit: cover;
  border-radius: 10px;
  margin-bottom: 20px;
}
