.pic-type {
  width: 28px;
  object-fit: cover;
  margin-right: 10px;
}

.img-file {
  object-fit: cover;
  width: 100%;
  height: 200px;
}

.trash-file {
  margin-top: 20px;
  transform: translateY(16px);
}
a:link {
  text-decoration: none;
  color: rgb(36, 36, 36);
}

.card-title {
  font-size: 17px;
  font-weight: bold;
  display: flex;
  align-items: center;
}

.allcards {
  margin-top: 20px;
}

/* select css */
.option {
  background-color: #fffffe;
  color: #000000;
  font-weight: 500;
  font-size: 16px;
}

.search-box {
  width: fit-content;
  height: fit-content;
  position: relative;
}
.input-search {
  height: 50px;
  width: 50px;
  border-style: none;
  padding: 10px;
  font-size: 18px;
  letter-spacing: 2px;
  outline: none;
  border-radius: 25px;
  transition: all 0.5s ease-in-out;
  background-color: #3da9fc;
  padding-right: 40px;
  color: #000;
}
.input-search::placeholder {
  color: rgba(255, 255, 255, 0.5);
  font-size: 18px;
  letter-spacing: 2px;
  font-weight: 100;
  color: #000;
}
.btn-search {
  width: 50px;
  height: 50px;
  border-style: none;
  font-size: 20px;
  font-weight: bold;
  outline: none;
  cursor: pointer;
  border-radius: 50%;
  position: absolute;
  right: 0px;
  color: #363636;
  background-color: transparent;
  pointer-events: painted;
}
.btn-search:focus ~ .input-search {
  width: 300px;
  border-radius: 0px;
  background-color: transparent;
  border-bottom: 1px solid rgba(255, 255, 255, 0.5);
  transition: all 500ms cubic-bezier(0, 0.11, 0.35, 2);
}
.input-search:focus {
  width: 300px;
  border-radius: 0px;
  background-color: transparent;
  border-bottom: 1px solid rgba(52, 51, 51, 0.5);
  transition: all 500ms cubic-bezier(0, 0.11, 0.35, 2);
}

/* responsiv */
@media (max-width: 720px) {
  .search-box {
    margin-left: 30px !important;
  }
  .allposts {
    margin: 0 auto;
  }
}

@media (max-width: 477px) {
  .img-file {
    height: 120px;
  }
  .pic-type {
    width: 24px;
    object-fit: cover;
  }
  .card-title {
    font-size: 13px;
    display: flex;
    align-items: center;
  }

  .input-search {
    font-size: 16px !important;
  }

  .input-search::placeholder {
    color: rgba(255, 255, 255, 0.5);
    font-size: 16px;
    letter-spacing: 2px;
    font-weight: 100;
    color: #000;
  }
}
