h1 {
  color: #094067;
  font-weight: bold;
  font-size: 38px;
}

.menu {
  text-align: center;
  padding: 15px 25px;
  background-color: #3da9fc;
  color: #fffffe;
  margin-top: 20px;
  font-weight: 500;
  transition: 0.3s;
  cursor: pointer;
}

.menu:hover {
  filter: brightness(0.9);
}

.greeting {
  color: rgb(181, 4, 4);
  
}
/* responsiv */
@media (max-width: 477px) {
  .menu {
    margin-top: 10px;
  }
}
