.banner {
  width: 100%;
  height: 300px;
  background-image: url(../assets/about.jpg);
  background-size: cover;
  background-attachment: fixed;
  background-repeat: no-repeat;
}

.about2 {
  border-top-right-radius: 49%;
  border-top-left-radius: 49%;
  border: 4px solid #f9aa32;
}

.gambar {
  display: grid;
  place-items: center;
}

h1 {
  color: #094067;
  font-weight: bold;
  font-size: 38px;
}
.abouth1 {
  margin-bottom: 50px;
}

.text {
  margin-top: 30px;
  font-size: 20px;
  color: rgb(85, 84, 84);
}

/* responsiv */

@media (max-width: 477px) {
  h1 {
    font-size: 30px !important;
  }

  .about2 {
    width: 220px;
    margin-top: -20px;
    margin-bottom: -20px;
  }

  .text {
    font-size: 18px;
    margin-left: 15px;
  }
}

@media (min-width: 830px) {
  .text {
    margin-left: -80px;
  }
}
