.footer {
  color: #888787;
  font-size: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.fa-instagram {
  margin-left: 15px;
  font-size: 25px;
  cursor: pointer;
  transition: 0.3s;
}

.fa-github {
  margin-left: 10px;
  font-size: 25px;
  cursor: pointer;
  transition: 0.3s;
}

a {
  text-decoration: none;
  color: inherit;
  transition: 0.3s;
}

a:hover {
  color: inherit;
  filter: brightness(0.8);
}
@media (max-width: 477px) {
  .footer {
    font-size: 13px;
  }

  .fa-instagram,
  .fa-github {
    font-size: 22px;
  }
}
