.singlephoto {
  width: 100%;
  height: auto;
  max-height: 400px;
  object-fit: contain;
  border-radius: 10px;
}

.singleposttitle {
  text-align: center;
  margin: 10px;
  font-family: "Josefin Sans", sans-serif;
  font-size: 28px;
  font-weight: bold;
  color: rgb(27, 27, 27);
}

.edit {
  float: right;
  font-size: 20px;
}

.singleposticon {
  margin-left: 10px;
  cursor: pointer;
  font-weight: bold;
}

.singleposticon:first-child {
  color: teal;
}

.singleposticon:last-child {
  color: tomato;
}

.info {
  margin-bottom: 20px;
  display: flex;
  justify-content: space-between;
  font-size: 15px;
  color: #b39656;
  font-family: "Varela Round", sans-serif;
}

.desc {
  color: #666;
  font-size: 17px;
  line-height: 25px;
}

.desc::first-letter {
  margin-left: 20px;
  font-size: 30px;
  font-weight: 600;
}

@media (max-width: 477px) {
  .singleposttitle {
    font-size: 22px !important;
  }

  .desc > * {
    font-size: 14px !important;
  }
}
