h1 {
  color: #094067;
  font-weight: bold;
  font-size: 38px;
}

.classb {
  background-color: #3da9fc;
  width: 300px;
  color: #fffffe;
  font-weight: bold;
  font-size: 18px;
  margin-left: 35px;
  margin-bottom: 10px;
  margin-top: 15px;
  text-align: center;
  padding: 10px 15px 10px 15px;
  border-radius: 30px;
  transition: 0.3s;
  cursor: pointer;
}

.classb:hover {
  filter: brightness(0.8);
}

.senin {
  font-size: 18px;
}

h5 {
  font-size: 24px;
}

.styleB {
  display: none;
}

.styleA {
  display: none;
}

@media (max-width: 477px) {
  h1 {
    font-size: 28px;
  }

  h5 {
    font-size: 20px;
  }
  .senin {
    font-size: 17px;
  }

  .switch {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .classb {
    margin-left: 0;
  }
}
