h1 {
  color: #094067;
  font-weight: bold;
  font-size: 38px;
}

.blogphoto {
  width: 100%;
  object-fit: cover;
  height: 150px;
  max-height: 450px;
  margin-top: 20px;
  border-radius: 10px;
}

.allposts {
  padding: 10px;
  margin-top: 20px;
  margin-bottom: 20px !important;
  margin: 0 10px 0 10px;
  box-sizing: border-box;
  /* padding: 10px; */
  box-shadow: 1px 2px 5px -1px rgba(0, 0, 0, 0.4);
}

.postinfo {
  text-align: center;
}

.postcat {
  color: #be9656;
  font-size: 12px;
  margin-top: 15px;
  margin-right: 10px;
  cursor: pointer;
}

.posttitle {
  font-weight: 700;
  font-size: 17px;
  margin-top: 15px;
  text-align: center;
  cursor: pointer;
}

.postdate {
  font-style: italic;
  font-size: 13px;
  margin-top: 15px;
}

.postdesc {
  font-size: 14px;
  color: #444;
  line-height: 24px;
  margin-top: 15px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
}

/* select css */
.option {
  background-color: #fffffe;
  color: #000000;
  font-weight: 500;
  font-size: 16px;
}

.search-box {
  width: fit-content;
  height: fit-content;
  position: relative;
  margin-left: 20px;
}
.input-search {
  height: 50px;
  width: 50px;
  border-style: none;
  padding: 10px;
  font-size: 18px;
  letter-spacing: 2px;
  outline: none;
  border-radius: 25px;
  transition: all 0.5s ease-in-out;
  background-color: #3da9fc;
  padding-right: 40px;
  color: #000;
}
.input-search::placeholder {
  color: rgba(255, 255, 255, 0.5);
  font-size: 18px;
  letter-spacing: 2px;
  font-weight: 100;
  color: #000;
}
.btn-search {
  width: 50px;
  height: 50px;
  border-style: none;
  font-size: 20px;
  font-weight: bold;
  outline: none;
  cursor: pointer;
  border-radius: 50%;
  position: absolute;
  right: 0px;
  color: #363636;
  background-color: transparent;
  pointer-events: painted;
}
.btn-search:focus ~ .input-search {
  width: 300px;
  border-radius: 0px;
  background-color: transparent;
  border-bottom: 1px solid rgba(255, 255, 255, 0.5);
  transition: all 500ms cubic-bezier(0, 0.11, 0.35, 2);
}
.input-search:focus {
  width: 300px;
  border-radius: 0px;
  background-color: transparent;
  border-bottom: 1px solid rgba(52, 51, 51, 0.5);
  transition: all 500ms cubic-bezier(0, 0.11, 0.35, 2);
}

.fa-trash-can {
  color: red;
  font-weight: bold;
  margin-right: 10px;
  cursor: pointer;
  transition: 0.3s;
}

.fa-pen-to-square {
  color: green;
  font-weight: bold;
  cursor: pointer;
  transition: 0.3s;
}

.fa-pen-to-square:hover {
  filter: brightness(0.8);
}
.fa-trash-can:hover {
  filter: brightness(0.8);
}
/* responsiv */
@media (max-width: 720px) {
  .search-box {
    margin-left: 0;
  }
  .allposts {
    margin: 0 auto;
  }
}

