.notes-pic {
  width: 100%;
  object-fit: cover;
  height: 200px;
  max-height: 450px;
  margin-top: 20px;
  box-shadow: 0px 0px 3px 1px rgba(0, 0, 0, 0.41);
}

.button-notes {
  width: 100%;
  background-color: #3da9fc;
  color: #fffffe;
  font-size: 15px;
  font-weight: bold;
  border: none;
  padding: 3px 5px 3px 5px;
  transition: 0.1s;
}

.button-notes:hover {
  filter: brightness(0.9);
}

@media (max-width: 477px) {
  .notes-pic {
    height: 120px;
    max-height: 200px;
  }
  .button-notes {
    padding: 2px 4px 2px 2px;
    font-size: 12px;
  }
}
